body {
	margin: 0;
	padding: 0;
	list-style: none;
	font-family: 'NanumBarunGothic, Sans serif';
	font-size: 1rem;
}

/*a { text-decoration: none; color: #333;}*/
a {
	text-decoration: none;
	color: #333;
}

a:hover {
	text-decoration: underline;
}

img {
	vertical-align: top;
}

body {
	font-family: 'NanumBarunGothic';
	overflow-x: hidden;
}

.guide {
	width: 1280px;
	margin: 0 auto;
	position: relative;
}

@media (max-width: 1330px) {
	.guide {
		width: 95%;
	}
}

.block {
	display: block;
}

.blind {
	font-size: 0;
	position: absolute;
	top: -999999px;
}

.mid_helper {
	display: inline-block;
	height: 100%;
	vertical-align: middle;
}

.text_cut {
	display: inline-block;
	width: 100%;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
}

.indent {
	text-indent: -1rem;
	padding-left: 1rem;
}

.navi_tab {
	width: 100%;
	position: absolute;
	z-index: 9999;
	width: 100%;
	left: 0;
	top: 0;
	text-align: center;
}

.navi_tab a {
	display: block;
	position: absolute;
	left: 0;
	top: -9999px;
	z-index: 1;
	width: 100%;
	height: 40px;
	line-height: 40px;
	background-color: #333;
	color: #fff;
	font-size: 1.1rem;
	font-weight: bold;
}

.navi_tab a:hover,
.navi_tab a:focus {
	top: 0;
}

.slick-prev,
.slick-next {
	width: 59px;
	height: 59px;
	background: #e2e6ec;
	text-indent: -999px;
	overflow: hidden;
	-webkit-background-size: auto auto;
			background-size: auto;
	background-repeat: no-repeat;
	background-position: center;
}

.slick-prev:hover,
.slick-next:hover {
	background: #8e8e8e;
	color: #fff;
}

.slick-prev {
	left: -90px;
	background-image: url("../images/main/latest_left.gif");
}

.slick-prev:hover, .slick-prev:focus {
	background: #e2e6ec;
	background-image: url("../images/main/latest_left.gif");
	-webkit-background-size: auto auto;
			background-size: auto;
	background-repeat: no-repeat;
	background-position: center;
}

.slick-next {
	right: -90px;
	background-image: url("../images/main/latest_right.gif");
}

.slick-next:hover, .slick-next:focus {
	background: #e2e6ec;
	background-image: url("../images/main/latest_right.gif");
	-webkit-background-size: auto auto;
			background-size: auto;
	background-repeat: no-repeat;
	background-position: center;
}

.slick-prev:before,
.slick-next:before {
	display: none;
}

button {
	border: none;
	background-color: transparent;
}

