/* mobile font resizing */
@media (max-width: 380px) {
	html {
		font-size: 11px;
	}
}

@media (min-width: 381px) and (max-width: 580px) {
	html {
		font-size: 12px;
	}
}

@media (min-width: 581px) and (max-width: 767px) {
	html {
		font-size: 12.5px;
	}
}

@media (min-width: 581px) and (max-width: 767px) {
	html {
		font-size: 13px;
	}
}

@media (min-width: 768px) and (max-width: 991px) {
	html {
		font-size: 13.5px;
	}
}

@media (min-width: 992px) {
	html {
		font-size: 14px;
	}
}

/* error */
.pop-view {
	position: relative;
	width: 100%; 
	height: 100%;
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
}
.pop-view * {
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
}
.er_wrapper {
	position: absolute;
	width: 100%;
	height: 100%;
	padding: 20px;
}

.er_wrapper .er_tbl {
	display: table;
	table-layout: fixed;
	width: 100%;
	height: 100%;
	/*width: 95%;
	max-width: 850px;
	left: 0;
	right: 0;
	margin: 0 auto;*/
}

.er_wrapper .er_td {
	display: table-cell;
	width: 100%;
	height: 100%;
	vertical-align: middle;
	text-align: center;
}

.er_wrapper .er_wrap {
	display: table;
	background-image: url("../images/sub/vision_bg.png");
	-webkit-background-size: auto auto;
	background-size: auto;
	background-repeat: repeat;
	background-position: left top;
	width: 100%;
	height: 100%;
	padding: 10px;
	/*-webkit-box-sizing: border-box;
	   -moz-box-sizing: border-box;
			box-sizing: border-box;*/
}

.er_wrapper .er_box {
	display: table-cell;
	vertical-align: middle;
	width: 100%;
	height: 100%;
	padding: 50px;
	background: #fff;
	text-align: center;
	/*-webkit-box-sizing: border-box;
	   -moz-box-sizing: border-box;
			box-sizing: border-box;*/
}

.er_wrapper .er_box > img {
	margin-bottom: 20px;
}

.er_wrapper .er_box > h5 {
	color: #6369bd;
	font-size: 2.133rem;
	margin-bottom: 10px;
}

.er_wrapper .er_box > p {
	color: #666;
	font-size: 1.2rem;
	word-break: keep-all;
}

.er_wrapper .er_box .btn_er_box {
	max-width: 545px;
	width: 95%;
	border-top: 1px solid #e5e5e5;
	padding-top: 30px;
	margin: 25px auto 0;
}

.er_wrapper .er_box .btn_er_box a {
	width: 125px;
	height: 34px;
	line-height: 34px;
	text-align: center;
	display: inline-block;
	font-size: 1.133rem;
	color: #fff;
	margin-left: 6px;
}

.er_wrapper .er_box .btn_er_box a:first-child {
	margin-left: 0;
}

.er_wrapper .er_box .btn_er_box a.prev_link {
	border: 1px solid #ccc;
	background: #fff;
	color: #333;
}

.er_wrapper .er_box .btn_er_box a.join_link {
	border: 1px solid #6469bb;
	background: #6469bb;
}

.er_wrapper .er_box .btn_er_box a.home_link {
	border: 1px solid #45495b;
	background: #45495b;
}

@media (max-width: 580px) {
	.er_wrapper .er_box {
		padding: 40px 10px;
	}
	.er_wrapper .er_box > h5 {
		font-size: 1.666rem;
	}
	.er_wrapper .er_box .btn_er_box a {
		width: 25%;
	}
}

@media (max-width: 480px) {
	.er_wrapper .er_box > h5 {
		font-size: 1.333rem;
		margin-bottom: 7px;
	}
	.er_wrapper .er_box > p {
		font-size: 1rem;
	}
	.er_wrapper .er_box .btn_er_box {
		padding-top: 20px;
	}
}

@media (max-width: 480px) {
	.er_wrapper .er_box .btn_er_box a {
		font-size: 1.067rem;
	}
	.er_wrapper .er_box img {
		width: 100px;
	}
}